<template>
  <div class="hello">
    <mapbox
      access-token="pk.eyJ1IjoiaW5kZWVkMTMiLCJhIjoiY2pvenN6eWFvMGJ1bjNybno3b253bnM4ZyJ9.f3-G0uZ9Rd1Tjd4WMddwUg"
      @map-load="mapLoaded"
      @geolocate-error="geolocateError"
      
      :map-options="{
      container: 'map',
  style: 'mapbox://styles/mapbox/light-v9',
  center: [17.107748, 48.148598],
  zoom: 10
}"
      :geolocate-control="{
  show: true,
  position: 'top-left'
}"
      :scale-control="{
  show: true,
  position: 'top-left'
}"
      :fullscreen-control="{
  show: true,
  position: 'top-left'
}"
@geolocate-geolocate="userPosition"
    ></mapbox>

    <md-card>
      <md-field>
        <label for="font">Oblasť</label>
        <md-select v-model="selectedPolygon">
          <md-option value="Všetko">Všetko</md-option>
          <md-option
            v-for="polygon in allPolygons"
            :key="polygon.name"
            :value="polygon.name"
          >{{polygon.name}}</md-option>
        </md-select>
      </md-field>

      <div>
      Vzdialenosť <br>
      <input type="range" v-model.number="distance"> {{ distance }}km
    </div>
    </md-card>
  </div>
</template>

<script>
import Mapbox from "mapbox-gl-vue";
export default {
  name: "HelloWorld",
  data() {
    return {
      distance: 100,
      polygonNames: [],
      selectedPolygon: "",
      allPolygons: [],
      pointFeature: {
        type: "Feature",
        geometry: null,
        properties: {
           title: null,
           icon: "harbor"
        }
      },
      geoJsonData: []
    };
  },
  components: {
    mapbox: Mapbox
  },
  methods: {
    userPosition(p){
      console.log(p._userLocationDotMarker._lngLat.lng);
      console.log(p._userLocationDotMarker._lngLat.lat);
    },
    mapLoaded(m) {
      this.map = m;
      /*this.map.addLayer({
        "id": "points",
        "type": "symbol",
        "source": {
            "type": "geojson",
            "data": {
                "type": "FeatureCollection",
                "features": [{
                    "type": "Feature",
                    "geometry": {
                        "type": "Point",
                        "coordinates": [17.305719,48.2020939998891]
                    },
                    "properties": {
                        "title": "Mapbox DC",
                        "icon": "monument"
                    }
                }, {
                    "type": "Feature",
                    "geometry": {
                        "type": "Point",
                        "coordinates": [17.305719,47.2020939998891]
                    },
                    "properties": {
                        "title": "Mapbox SF",
                        "icon": "harbor"
                    }
                }]
            }
        },
        "layout": {
            "icon-image": "circle-15",
            "text-font": ["Open Sans Semibold", "Arial Unicode MS Bold"],
            "text-offset": [0, 0.6],
            "text-anchor": "top"
        }
    });*/
    },

    geolocateError(control, positionError) {
      console.log(positionError);
    },
    getNamesOfAllPolygons() {
      this.$http
        .get("http://localhost:3000/getPolygonNames")
        .then(function(data) {
          var i;
          this.allPolygons = data.body;
          for (i = 0; i < data.body.length; i++) {
            this.polygonNames.push(data.body[i].name);
          }
        });
    }
  },
  watch: {
    selectedPolygon: function(val) {
      this.selectedPolygon = val;
      if (val == "Všetko") console.log("daj všetky zastávky");
      else {
        console.log(
          this.allPolygons[this.polygonNames.indexOf(this.selectedPolygon)].way
        );
        this.$http
          .post("http://localhost:3000/getPlatformsByPolygon", {
            way: this.allPolygons[
              this.polygonNames.indexOf(this.selectedPolygon)
            ].way
          })
          .then(function(data) {
            var i;
            this.geoJsonData = [];
            for (i = 0; i < data.body.length; i++) {
              this.pointFeature.geometry = JSON.parse(data.body[i].geometry);
              this.pointFeature.properties.title = data.body[i].name;
              this.geoJsonData.push(this.pointFeature);
            }
            console.log(this.geoJsonData);
            var kkk = {
              id: "points",
              type: "symbol",
              source: {
                type: "geojson",
                data: {
                  type: "FeatureCollection",
                  features: this.geoJsonData
                }
              },
              layout: {
                "icon-image": "circle-15",
                "text-font": ["Open Sans Semibold", "Arial Unicode MS Bold"],
                "text-offset": [0, 0.6],
                "text-anchor": "top"
              }
            };
            console.log(kkk);
            this.map.addLayer(kkk);

            console.log(data);
          });
      }
    }
  },
  created() {
    this.getNamesOfAllPolygons();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#map {
  width: 100%;
  height: 500px;
}
</style>



